/**********************************/
/***** HEADERS ********************/
/**********************************/
.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.grad-header {
  position: relative;
  max-width: 800px;
}
.header-wrap {
  background: #1c1c1c;
  padding: 13px 0;
  z-index: 1;
}
.search-headline {
  float: left;
  margin-bottom: 4px;
}
header h1 {
  float: left;
  color: white;
  font-size: 30px;
  margin: 0;
}
.search-input input::-webkit-input-placeholder, 
.search-input input:-ms-input-placeholder, 
.search-input input::-ms-input-placeholder, 
.search-input input::-webkit-input-placeholder {
  color: #868686;
}
.search-input input::-webkit-input-placeholder, /* WebKit, Blink, Edge */
.search-input input:-ms-input-placeholder, /* Internet Explorer 10-11 */
.search-input input::-ms-input-placeholder, /* Microsoft Edge */
.search-input input::placeholder {
  color: #868686;
}
.search-input input:-moz-placeholder, /* Mozilla Firefox 4 to 18 */
.search-input input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #868686;
  opacity: 1;
}
.search-input input {
  color: #fff;
  background: #383838;
  border-color: #383838;
  margin: 0 0 7px;
}
.search-input .form-group {
  margin-bottom: 0;
}
.search-icon {
  position: absolute;
  color: #868686;
  top: 45px;
  right: 23px;
}

@media (min-width: 480px) {
  .header-wrap {
    padding: 20px 0;
  }
  header h1 {
    font-size: 36px;
  }
  .search-icon {
    top: 50px;
  }
}

/**************************************/
/***** PROFILE CARDS ******************/
/**************************************/

/* ------ Bootstrap Overrides ------- */
.media-body {
  padding-left: 10px;
  padding-top: 10px;
}
/* ---------------------------------- */
/* enable absolute positioning */
.profile-directory {
  max-width: 800px;
  margin: auto;
  position: relative;
  padding: 0 10px;
}
.profile-directory h1 {
  font-family: Roboto Condensed;
  font-weight: 700;
  text-align: center;
  margin: 35px auto 10px;
}
.card {
  border-top: 1px solid #d0d0d0;
  padding: 20px 0 10px;
  margin-bottom: 10px;
}
.card:first-child {
  border-top: none;
}
.profile-thumbnail-wrap {
  width: 100px;
  height: 100px;
  text-indent: -50%;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid #e2e2e2;
}
.profile-thumbnail {
  margin-bottom: 15px;
}
.profile-thumbnail img {
  object-fit: cover;
  box-shadow: none;
  border-radius: 50%;
  border: 1px solid #e2e2e2;
}
.media-left > a img:hover {
  opacity: 0.7;
}
.media-body h4 {
  font-size: 23px;
  font-weight: 700;
  line-height: 22px;
  margin-bottom: 10px;
}
.media-body h4 a {
  font-size: 23px;
  font-weight: 700;
  line-height: 22px;
  margin-bottom: 10px;
  color: #383838;
}
.media-body h4 a:hover {
  color: #00b5e0;
  text-decoration: none;
}

.modal-header {
  color: #fff;
  background: #00b5e0;
}
.grad-form {
  margin-top: 15px;
  margin-bottom: 40px;
  max-width: 800px;
}
.grad-form-group {
  position: absolute;
  left: 0;
  top: 0;
}
.helper {
  color: #868686;
  font-weight: 300;
  margin-left: 4px;
}
.helper-asterisk {
  margin-left: 1px;
}
.helper-asterisk-red {
  color: #a94442;
}
textarea {
  resize: none;
}
.isActive {
  display: block;
  float: left;
  margin-right: 6px;
}

/* --- Image & Update Button ------------------ */
/* --- See additional styling in Search.css --- */
.form-thumbnail {
  position: relative;
  display: block;
  float: left;
}
.form-thumbnail img {
  position: relative;
  float: left;
  border: 1px solid #e2e2e2;
}
.choose-btn {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #8ec957;
  border: 1px solid #8ec957;
  opacity: 0;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}
.choose-btn h3,
.missing-btn h3 {
  text-align: center;
  color: #fff;
  font-size: 22px;
  padding-top: 6px;
}
.form-thumbnail:hover .choose-btn {
  opacity: 1;
}
.missing-btn {
  position: relative;
  float: left;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #e1e0e0;
  border: 1px solid #e1e0e0;
}
#image {
  position: relative;
  background: #8ec957;
  z-index: 1000;
  opacity: 0;
  cursor: pointer;
  width: 100px;
  height: 100px;
  font-size: 24px;
  border-radius: 50%;
}

/* --- Resume & Update Button --------- */
.form-resume {
  position: relative;
  float: left;
  margin-bottom: 15px;
  margin-left: 20px;
}
.form-resume img {
  position: relative;
  border-radius: 50%;
  border: 1px solid #e2e2e2;
}
.form-resume:hover .choose-btn {
  opacity: 1;
}
#resume {
  position: relative;
  background: #8ec957;
  z-index: 1000;
  opacity: 0;
  cursor: pointer;
  width: 100px;
  height: 100px;
  font-size: 24px;
  border-radius: 50%;
}


